html {
  font-size: 10px;
}

body {
  margin: 0;
  background-color: #000;
  color: #fff;
  font-size: 2rem;
}

button {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
}

.hidden {
  opacity: 0;
}

.application {
  height: calc(100vh - 20px);
  width: calc(100vw - 35px);
  max-width: 1440px;
  margin: 0 auto;
  margin-top: 20px;
}

.container {
  height: 100%;
  position: relative;
  left: 0;
  transition: left 2s ease;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10%;
}

.content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 80%;
}

.content div {
  width: 100%;
}
.content div.category-image {
  width: 195px;
}

.hide {
  left: -2000px;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10%;
  position: absolute;
  bottom: 0;
  width: calc(100vw - 100px);
}

.footer-logo {
  width: 85px;
  height: 31px;
  background-image: url("../img/svg/rs_school.svg");
}

.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 15%;
}
.info a {
  text-decoration: none;
  color: #fff;
}

.button {
  width: 287px;
  height: 62px;
  background-color: transparent;
  color: #fff;
  border: 1.5px solid white;
  border-radius: 60px;
  transition: all 0.5s ease;
}
.button:hover {
  transform: scale(1.02);
  box-shadow: 5px 5px 15px rgba(255, 255, 255, 0.7);
  background-color: #ffbca2;
  color: #000;
}

.button_colored {
  border: none;
  color: #000;
  background-color: #ffbca2;
}

.button_element {
  margin-right: 20px;
}

.home-route,
.categories-route,
.settings-route {
  cursor: pointer;
}

.container-main {
  background-image: url("../img/bg-main.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center bottom;
}

.settings {
  width: 36px;
  height: 36px;
  margin-left: auto;
  background-image: url("../img/svg/settings.svg");
  transition: transform 0.8s ease-in-out;
}
.settings:hover {
  transform: rotate(180deg);
}

.logo {
  width: 100%;
  height: 165px;
  background-image: url("../img/logo-main.png");
  background-repeat: no-repeat;
  background-position: center;
}

.content .button-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 41%;
}

.setting-title {
  margin: 0;
  margin-left: 10px;
  position: relative;
  font-size: 2rem;
  line-height: 2.8rem;
}
.setting-title::before {
  content: "";
  background-image: url("../img/svg/left_arrow.svg");
  width: 10px;
  height: 20px;
  position: absolute;
  top: 4px;
  left: -15px;
}

.exit {
  width: 36px;
  height: 36px;
  background-image: url("../img/svg/exit.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.content-settings {
  margin-top: 3%;
}
.content-settings > div {
  margin-bottom: 2%;
}
.content-settings .setting-time {
  display: flex;
  align-items: center;
  margin-bottom: 0;
}
.content-settings .setting-time button {
  width: 20px;
  height: 20px;
  text-align: center;
  border-radius: 100%;
  background-color: #fff;
  font-size: 20px;
}
.content-settings .setting-time button span {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.content-settings .setting-volume {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0;
  width: 16%;
}
.content-settings .setting-volume button {
  width: 36px;
  height: 36px;
  background-repeat: no-repeat;
  background-position: center;
}
.content-settings .setting-timer-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 8%;
}
.content-settings .setting-timer-wrapper .setting-timer {
  width: 50px;
  height: 24px;
  border-radius: 34px;
  background-color: #ffbca2;
  position: relative;
}
.content-settings .setting-timer-wrapper .setting-timer .slider {
  width: 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 100%;
  position: absolute;
}

.subsetting-title {
  margin: 0;
  margin-bottom: 1%;
  font-size: 2.5rem;
}

.mute {
  background-image: url("../img/svg/mute.svg");
}

.sound {
  background-image: url("../img/svg/volume.svg");
}

#volume-bar {
  appearance: none;
  height: 3px;
  width: 15%;
  cursor: pointer;
  border-radius: 4.5px;
  background: linear-gradient(to right, #ffbca2 0%, #ffbca2 30%, #a4a4a4 30%, #a4a4a4 100%);
}
#volume-bar::-webkit-slider-thumb {
  appearance: none;
  width: 16px;
  height: 16px;
  background-color: #fff;
  border-radius: 100%;
}

.on {
  left: 0;
  right: auto;
}

.off {
  left: auto;
  right: 0;
}

#time-input {
  width: 50px;
  padding: 0;
  border: none;
  background-color: transparent;
  position: relative;
}

.time-input-value {
  position: absolute;
  left: 36px;
}

.setting-buttons {
  display: flex;
  justify-content: flex-end;
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  width: 550px;
  list-style-type: none;
}
.navigation-links {
  width: 260px;
}
.navigation-links li:hover {
  color: #ffbca2;
  cursor: pointer;
}

.content_categories {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  column-gap: 15px;
  row-gap: 5px;
  justify-items: center;
  max-width: 1440px;
}

.category {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 195px;
  max-height: 239px;
}
.category-statistics {
  flex-basis: 65%;
  padding-right: 5px;
  text-align: right;
}
.category-name {
  margin: 0;
  flex-basis: 30%;
  text-align: center;
  font-size: 4rem;
  color: #ffbca2;
}
.category-image {
  height: 206px;
  margin-top: 10px;
  border-radius: 20px;
}

.logo-small {
  width: 153px;
  height: 60px;
  background-image: url("../img/logo-small.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}