.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  width: 550px;
  list-style-type: none;
  &-links {
    width: 260px;
    & li:hover {
      color: $color-active;
      cursor: pointer;
    }
  }
}

.content_categories {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  column-gap: 15px;
  row-gap: 5px;
  justify-items: center;
  max-width: 1440px;
}

.category {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 195px;
  max-height: 239px;
  &-statistics {
    flex-basis: 65%;
    padding-right: 5px;
    text-align: right;
  }
  &-name {
    margin: 0;
    flex-basis: 30%;
    text-align: center;
    font-size: 4rem;
    color: $color-active;
  }
  &-image {
    height: 206px;
    margin-top: 10px;
    border-radius: 20px;
  }
}

.logo-small {
  width: 153px;
  height: 60px;
  background-image: url('../img/logo-small.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
